button {
  background-color: transparent;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  padding-left: 20px;
}

body ::-webkit-scrollbar {
  background-color: transparent;
}

textarea {
  resize: none;
}

.sticky-container {
  display: flex;
  width: 450px;
  height: 280px;
  margin: 10px;
  margin-top: 60px;
  background-image: url(/static/media/stickyNote.8d82df47.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.sticky-content {
  width: 410px;
  height: 220px;
  background-color: transparent;
  margin: 20px;
}

.sticky-content .header {
  display: flex;
  justify-content: center;
  margin: 20px 50px 0px 50px;
  color: green;
}

.input-title {
  border: none;
  background: transparent;
  font-size: 32px;
  color: green;
  font-weight: bold;
  text-align: center;
  width: 250px;
}

.input-title:focus {
  outline: none;
}
.input-title:focus::-webkit-input-placeholder {
  color: transparent;
}
.input-title:focus::placeholder {
  color: transparent;
}

.input-title::-webkit-input-placeholder {
  font-size: 32px;
  text-align: center;
  color: green;
  font-weight: bold;
}

.input-title::placeholder {
  font-size: 32px;
  text-align: center;
  color: green;
  font-weight: bold;
}

.sticky-content .body {
  margin: 0px 10px 0px 10px;
  height: 70px;
  overflow-y: auto;
  color: black;
  text-align: center;
  font-style: italic;
}

.input-body {
  height: 40px;
  width: 350px;
  background: transparent;
  border: none;
  text-align: center;
  font-size: 16px;
  font-style: italic;
}

.input-body:focus {
  outline: none;
}

.input-body:focus::-webkit-input-placeholder {
  color: transparent;
}

.input-body:focus::placeholder {
  color: transparent;
}

.input-body::-webkit-input-placeholder {
  text-align: center;
}

.input-body::placeholder {
  text-align: center;
}

.sticky-content .footer {
  display: flex;
  justify-content: space-between;
  align-content: flex-end;
  margin: 10px 20px;
}
.sticky-content .footer .date {
  color: green;
  display: flex;
}

.edit-btn {
  color: black;
  font-weight: bold;
}

.edit-btn:hover {
  color: white;
}

.delete-btn {
  color: red;
  font-weight: bold;
}
.delete-btn:hover {
  color: white;
}
.submit-btn {
  color: red;
  font-weight: bold;
}
.submit-btn:hover {
  color: white;
}

.input-date {
  width: 20px;
  font-size: 16px;
  background: transparent;
  border: none;
  color: green;
}
.input-date::-webkit-input-placeholder {
  color: green;
}
.input-date::placeholder {
  color: green;
}

.input-date .year {
  width: 40px;
}

.input-date:focus {
  outline: none;
}
.input-date:focus::-webkit-input-placeholder {
  color: transparent;
}
.input-date:focus::placeholder {
  color: transparent;
}

.delete-btn:disabled {
  color: gray;
  opacity: 0.3;
}
.edit-btn:disabled {
  color: gray;
  opacity: 0.3;
}

body {
  background-color: rgb(53, 53, 53);
}

.stickies-board {
  width: 1410px;
}

.month-container {
  display: flex;
  width: 1410px;
  overflow-x: scroll;
}

[id^="id-"] {
  display: flex;
  width: 1410px;
  overflow-x: scroll;
}

.month-title {
  color: white;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 0.5px solid green;
}
.to-sticky-btn {
  position: fixed;
  top: 10px;
  left: 50px;
  display: flex;
  justify-items: start;
}
.create-sticky {
  color: white;
  border: 1px solid white;
  border-radius: 10px;
  font-size: 20px;
  width: 300px;
  height: 35px;
  text-align: center;
}
.create-sticky:hover {
  color: green;
}
.create-sticky:active {
  color: red;
}

.scroll-btns-container {
  display: flex;
  justify-content: space-between;
  margin-left: 50px;
  margin-right: 50px;
}

#slideLeft,
#slideRight {
  font-size: 30px;
  color: white;
}
#slideLeft:hover,
#slideRight:hover {
  color: green;
}
#slideLeft:active,
#slideRight:active {
  color: red;
}

.years-btn-container {
  display: flex;
  justify-content: center;
}

.year-btn {
  text-align: center;
  font-size: 25px;
  color: white;

  margin-right: 10px;
  margin-left: 10px;
}

.year-btn:hover {
  color: red;
}

.year-btn:active {
  color: green;
}
.year-btn:focus {
  color: green;
}

.no-sticky {
  margin-top: 50%;
  text-align: center;
  color: white;
  -webkit-animation: colorNoSticky 2s 100;
          animation: colorNoSticky 2s 100;
}

@-webkit-keyframes colorNoSticky {
  0% {
    color: rgb(229, 208, 53);
  }
  25% {
    color: white;
  }
  75% {
    color: white;
  }
  100% {
    color: rgb(229, 208, 53);
  }
}

@keyframes colorNoSticky {
  0% {
    color: rgb(229, 208, 53);
  }
  25% {
    color: white;
  }
  75% {
    color: white;
  }
  100% {
    color: rgb(229, 208, 53);
  }
}

.modal-container {
  position: fixed;
  top: 50px;
  right: 50px;
  display: flex;
  flex-direction: column;
  background-color: rgb(136, 133, 133, 0.6);
  width: 400px;
  height: 120px;
  font-size: 20px;
  border: 1px solid white;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
}
.modal-header {
  margin-bottom: 30px;
  color: rgb(53, 180, 53);
}
.modal-footer {
  display: flex;
  justify-content: space-evenly;
}

.modal-btn {
  margin-left: 20px;
  margin-right: 20px;
  font-weight: bold;
  font-size: 20px;
  color: white;
}
.yes:hover {
  color: green;
}
.no:hover {
  color: red;
}

